import { AiOutlineMail, AiOutlinePhone } from 'react-icons/ai'
import { GrLocation } from 'react-icons/gr/index'
import './contact.css'

const Contact = () => {
    return (
        <div className="contact-me" id="contact">
            <div className="container">
            <h2>Contact Details</h2>
            <div className="contact-area">

                <div className="contact-info">
                    <h3>Contact Information</h3>
                    <p><GrLocation/> Bucharest/Romania </p>
                    <p><AiOutlineMail/> 
                        <a href="mailto:Nazar32341@gmail.com">
                            Nazar32341@gmail.com
                        </a> 
                    
                    </p>

                    <p><AiOutlinePhone/> 
                        <a href="tel:0040737189138">
                            +40737189138
                        </a> 
                    
                    </p>
                    
                </div>
                
                <form action="mailto:Nazar32341@gmail.com"

                    method="POST"
                    encType="multipart/form-data"
                    name="EmailForm">
                        <h3>Get In Touch</h3>
                        <input type="text" placeholder="Your Name"/>
                        <input type="text" placeholder="Email"/>
                        <textarea placeholder="Write you Message"/>
                        <button>Send</button>

                </form>
            </div>
            </div>
            
        </div>
    )
}

export default Contact
